<template>
  <div class="user-data-manager">
    <CommonHeader :isUserSide="true" />
    <div class="content-wrapper">
      <CommonNavigation />
      <div class="main-content">
        <div class="page-header">
          <h1></h1>
          <p></p>
        </div>

        <div class="data-stats">
          <a-row :gutter="16">
            <a-col :span="6">
              <a-card>
                <div class="stat-item">
                  <div class="stat-number">{{ dataStats.attendanceCount }}</div>
                  <div class="stat-label"></div>
                </div>
              </a-card>
            </a-col>
            <a-col :span="6">
              <a-card>
                <div class="stat-item">
                  <div class="stat-number">{{ dataStats.approvalCount }}</div>
                  <div class="stat-label"></div>
                </div>
              </a-card>
            </a-col>
            <a-col :span="6">
              <a-card>
                <div class="stat-item">
                  <div class="stat-number">{{ dataStats.fileCount }}</div>
                  <div class="stat-label"></div>
                </div>
              </a-card>
            </a-col>
            <a-col :span="6">
              <a-card>
                <div class="stat-item">
                  <div class="stat-number">{{ dataStats.activityCount }}</div>
                  <div class="stat-label"></div>
                </div>
              </a-card>
            </a-col>
          </a-row>
        </div>

        <div class="data-actions">
          <a-row :gutter="16">
            <a-col :span="8">
              <a-card title="" class="action-card">
                <template #extra>
                  <a-icon type="download" />
                </template>
                <p></p>
                <a-button type="primary" @click="exportData" :loading="exporting">
                  <a-icon type="download" />
                  
                </a-button>
              </a-card>
            </a-col>
            <a-col :span="8">
              <a-card title="" class="action-card">
                <template #extra>
                  <a-icon type="upload" />
                </template>
                <p></p>
                <a-upload
                  :showUploadList="false"
                  :beforeUpload="importData"
                  accept=".json"
                >
                  <a-button type="primary">
                    <a-icon type="upload" />
                    
                  </a-button>
                </a-upload>
              </a-card>
            </a-col>
            <a-col :span="8">
              <a-card title="" class="action-card">
                <template #extra>
                  <a-icon type="delete" />
                </template>
                <p></p>
                <a-button type="danger" @click="showClearConfirm">
                  <a-icon type="delete" />
                  
                </a-button>
              </a-card>
            </a-col>
          </a-row>
        </div>

        <div class="data-preview">
          <a-card title="">
            <a-tabs>
              <a-tab-pane key="attendance" tab="">
                <a-table
                  :columns="attendanceColumns"
                  :dataSource="attendanceData"
                  :pagination="{ pageSize: 5 }"
                  size="small"
                />
              </a-tab-pane>
              <a-tab-pane key="approval" tab="">
                <a-table
                  :columns="approvalColumns"
                  :dataSource="approvalData"
                  :pagination="{ pageSize: 5 }"
                  size="small"
                />
              </a-tab-pane>
              <a-tab-pane key="files" tab="">
                <a-table
                  :columns="fileColumns"
                  :dataSource="fileData"
                  :pagination="{ pageSize: 5 }"
                  size="small"
                />
              </a-tab-pane>
              <a-tab-pane key="activities" tab="">
                <a-table
                  :columns="activityColumns"
                  :dataSource="activityData"
                  :pagination="{ pageSize: 5 }"
                  size="small"
                />
              </a-tab-pane>
            </a-tabs>
          </a-card>
        </div>
      </div>
    </div>

    <!--  -->
    <a-modal
      title=""
      :visible="showClearModal"
      @ok="clearData"
      @cancel="showClearModal = false"
      okText=""
      cancelText=""
      okType="danger"
    >
      <p></p>
      <p></p>
      <ul>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
      </ul>
    </a-modal>
  </div>
</template>

<script>
import { defineComponent, ref, reactive, onMounted } from 'vue'
import { message } from 'ant-design-vue'
import CommonHeader from '@/components/CommonHeader.vue'
import CommonNavigation from '@/components/CommonNavigation.vue'
import {
  getAttendanceRecords,
  getApprovalRecords,
  getFileRecords,
  getUserActivities,
  exportUserData,
  importUserData,
  clearCurrentUserData
} from '@/utils/userDataManager'

export default defineComponent({
  name: 'UserDataManager',
  components: {
    CommonHeader,
    CommonNavigation
  },
  setup() {
    const exporting = ref(false)
    const showClearModal = ref(false)

    // 
    const dataStats = reactive({
      attendanceCount: 0,
      approvalCount: 0,
      fileCount: 0,
      activityCount: 0
    })

    // 
    const attendanceData = ref([])
    const approvalData = ref([])
    const fileData = ref([])
    const activityData = ref([])

    // 
    const attendanceColumns = [
      { title: '', dataIndex: 'date', key: 'date' },
      { title: '', dataIndex: 'checkInTime', key: 'checkInTime' },
      { title: '', dataIndex: 'checkOutTime', key: 'checkOutTime' },
      { title: '', dataIndex: 'statusText', key: 'statusText' }
    ]

    const approvalColumns = [
      { title: '', dataIndex: 'type', key: 'type' },
      { title: '', dataIndex: 'content', key: 'content' },
      { title: '', dataIndex: 'applyTime', key: 'applyTime' },
      { title: '', dataIndex: 'status', key: 'status' }
    ]

    const fileColumns = [
      { title: '', dataIndex: 'name', key: 'name' },
      { title: '', dataIndex: 'type', key: 'type' },
      { title: '', dataIndex: 'size', key: 'size' },
      { title: '', dataIndex: 'uploadTime', key: 'uploadTime' }
    ]

    const activityColumns = [
      { title: '', dataIndex: 'type', key: 'type' },
      { title: '', dataIndex: 'title', key: 'title' },
      { title: '', dataIndex: 'time', key: 'time' },
      { title: '', dataIndex: 'statusText', key: 'statusText' }
    ]

    // 
    const loadData = () => {
      const attendance = getAttendanceRecords()
      const approval = getApprovalRecords()
      const files = getFileRecords()
      const activities = getUserActivities()

      dataStats.attendanceCount = attendance.length
      dataStats.approvalCount = approval.length
      dataStats.fileCount = files.length
      dataStats.activityCount = activities.length

      attendanceData.value = attendance.slice(0, 10)
      approvalData.value = approval.slice(0, 10)
      fileData.value = files.slice(0, 10)
      activityData.value = activities.slice(0, 10)
    }

    // 
    const exportData = async () => {
      exporting.value = true
      try {
        const data = exportUserData()
        const blob = new Blob([JSON.stringify(data, null, 2)], { type: 'application/json' })
        const url = URL.createObjectURL(blob)
        const a = document.createElement('a')
        a.href = url
        a.download = `user_data_${data.username}_${new Date().toISOString().split('T')[0]}.json`
        document.body.appendChild(a)
        a.click()
        document.body.removeChild(a)
        URL.revokeObjectURL(url)
        message.success('')
      } catch (error) {
        message.error('')
        console.error(':', error)
      } finally {
        exporting.value = false
      }
    }

    // 
    const importData = (file) => {
      const reader = new FileReader()
      reader.onload = (e) => {
        try {
          const data = JSON.parse(e.target.result)
          if (importUserData(data)) {
            message.success('')
            loadData()
          } else {
            message.error('')
          }
        } catch (error) {
          message.error('')
          console.error(':', error)
        }
      }
      reader.readAsText(file)
      return false // 
    }

    // 
    const showClearConfirm = () => {
      showClearModal.value = true
    }

    // 
    const clearData = () => {
      try {
        if (clearCurrentUserData()) {
          message.success('')
          loadData()
        } else {
          message.error('')
        }
      } catch (error) {
        message.error('')
        console.error(':', error)
      } finally {
        showClearModal.value = false
      }
    }

    onMounted(() => {
      loadData()
    })

    return {
      exporting,
      showClearModal,
      dataStats,
      attendanceData,
      approvalData,
      fileData,
      activityData,
      attendanceColumns,
      approvalColumns,
      fileColumns,
      activityColumns,
      exportData,
      importData,
      showClearConfirm,
      clearData
    }
  }
})
</script>

<style scoped>
.user-data-manager {
  min-height: 100vh;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
}

.content-wrapper {
  display: flex;
  min-height: calc(100vh - 64px);
}

.main-content {
  flex: 1;
  padding: 24px;
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  border-radius: 12px;
  margin: 16px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
}

.page-header {
  margin-bottom: 24px;
  text-align: center;
}

.page-header h1 {
  color: #2c3e50;
  margin-bottom: 8px;
  font-size: 28px;
  font-weight: 600;
}

.page-header p {
  color: #7f8c8d;
  font-size: 16px;
}

.data-stats {
  margin-bottom: 24px;
}

.stat-item {
  text-align: center;
  padding: 16px;
}

.stat-number {
  font-size: 32px;
  font-weight: bold;
  color: #3498db;
  margin-bottom: 8px;
}

.stat-label {
  color: #7f8c8d;
  font-size: 14px;
}

.data-actions {
  margin-bottom: 24px;
}

.action-card {
  text-align: center;
  height: 100%;
}

.action-card p {
  margin-bottom: 16px;
  color: #7f8c8d;
}

.data-preview {
  margin-top: 24px;
}

.ant-table {
  background: white;
  border-radius: 8px;
}

.ant-card {
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}
</style> 