    // 
    const showModal = ref(false)
    const isEditing = ref(false)
    const formData = ref({
      id: '',
      name: '',
      username: '',
      email: '',
      role: '',
      status: '',
      avatar: ''
    })
    const formErrors = ref({
      id: '',
      name: '',
      username: '',
      email: '',
      role: '',
      status: ''
    })

    // 
    const showDeleteModal = ref(false)
    const userToDelete = ref(null)

    // 
    const goHome = () => {
      router.push('/')
    }
    
    // 
    const getUserInfo = () => {
      const user = localStorage.getItem('user')
      if (user) {
        try {
          const parsedUser = JSON.parse(user)
          
          // 
          const savedUserInfo = localStorage.getItem('savedUserInfo')
          if (savedUserInfo) {
            try {
              const parsedSavedInfo = JSON.parse(savedUserInfo)
              userInfo.value = {
                ...parsedUser,
                ...parsedSavedInfo, // 
                username: parsedSavedInfo.username || parsedUser.username || '', // 
                avatar: parsedSavedInfo.avatar || userInfo.value.avatar
              }
            } catch (error) {
              console.error(':', error)
              userInfo.value = {
                ...parsedUser,
                username: parsedUser.username || '',
                avatar: userInfo.value.avatar
              }
            }
          } else {
            userInfo.value = {
              ...parsedUser,
              username: parsedUser.username || '',
              avatar: userInfo.value.avatar
            }
          }
        } catch (error) {
          console.error(':', error)
        }
      }
    }

    // 
    const showAddModal = () => {
      isEditing.value = false
      formData.value = {
        id: '',
        name: '',
        username: '',
        email: '',
        role: '',
        status: '',
        avatar: ''
      }
      formErrors.value = {
        id: '',
        name: '',
        username: '',
        email: '',
        role: '',
        status: ''
      }
      showModal.value = true
    }

    // 
    const showEditModal = (user) => {
      isEditing.value = true
      formData.value = { 
        ...user,
        roleText: undefined,
        statusText: undefined
      }
      formErrors.value = {
        id: '',
        name: '',
        username: '',
        email: '',
        role: '',
        status: ''
      }
      showModal.value = true
    }

    // 
    const closeModal = () => {
      showModal.value = false
    }

    // 
    const validateForm = () => {
      let isValid = true
      formErrors.value = {
        id: '',
        name: '',
        username: '',
        email: '',
        role: '',
        status: ''
      }

      if (!formData.value.id) {
        formErrors.value.id = 'ID'
        isValid = false
      }
      if (!formData.value.name) {
        formErrors.value.name = ''
        isValid = false
      }
      if (!formData.value.username) {
        formErrors.value.username = ''
        isValid = false
      }
      if (!formData.value.email) {
        formErrors.value.email = ''
        isValid = false
      } else if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(formData.value.email)) {
        formErrors.value.email = ''
        isValid = false
      }
      if (!formData.value.role) {
        formErrors.value.role = ''
        isValid = false
      }
      if (!formData.value.status) {
        formErrors.value.status = ''
        isValid = false
      }

      // IDID
      if (!isEditing.value || formData.value.id !== users.value.find(u => u.id === formData.value.id)?.id) {
        const existingUser = users.value.find(user => user.id === formData.value.id)
        if (existingUser) {
          formErrors.value.id = 'ID'
          isValid = false
        }
      }

      const existingUsername = users.value.find(user => 
        user.username === formData.value.username && 
        (!isEditing.value || user.id !== formData.value.id)
      )
      if (existingUsername) {
        formErrors.value.username = ''
        isValid = false
      }

      return isValid
    }

    // 
    const submitForm = () => {
      if (validateForm()) {
        if (isEditing.value) {
          // 
          const index = users.value.findIndex(user => user.id === formData.value.id)
          if (index !== -1) {
            const updatedUser = {
              ...formData.value,
              roleText: formData.value.role === 'admin' ? '' : '',
              statusText: formData.value.status === 'active' ? '' : '',
              avatar: formData.value.avatar || `https://api.dicebear.com/7.x/avataaars/svg?seed=${formData.value.id}`
            }
            users.value[index] = updatedUser
            closeModal()
            alert('')
          }
        } else {
          // 
          const newUser = {
            ...formData.value,
            roleText: formData.value.role === 'admin' ? '' : '',
            statusText: formData.value.status === 'active' ? '' : '',
            avatar: `https://api.dicebear.com/7.x/avataaars/svg?seed=${formData.value.id}`,
            createTime: new Date().toISOString().split('T')[0]
          }
          users.value.unshift(newUser)
          closeModal()
          alert('')
        }
      }
    }

    // 
    const showDeleteConfirm = (user) => {
      userToDelete.value = user
      showDeleteModal.value = true
    }

    // 
    const closeDeleteModal = () => {
      showDeleteModal.value = false
      userToDelete.value = null
    }

    // 
    const confirmDelete = () => {
      if (userToDelete.value) {
        const index = users.value.findIndex(user => user.id === userToDelete.value.id)
        if (index !== -1) {
          users.value.splice(index, 1)
          closeDeleteModal()
          alert('')
        } else {
          alert('')
        }
      }
    }

    // 
    const handleUserInfoUpdate = () => {
      getUserInfo() // 
    }

    onMounted(() => {
      getUserInfo()
      // 
      window.addEventListener('userInfoUpdated', handleUserInfoUpdate)
    })

    onUnmounted(() => {
      // 
      window.removeEventListener('userInfoUpdated', handleUserInfoUpdate)
    })

    return {
      userInfo,
      users,
      goHome,
      showModal,
      isEditing,
      formData,
      formErrors,
      showAddModal,
      showEditModal,
      closeModal,
