        formErrors.value.content = ''
        hasError = true
      } else {
        formErrors.value.content = ''
      }

      if (!announcementForm.value.priority) {
        formErrors.value.priority = ''
        hasError = true
      } else {
        formErrors.value.priority = ''
      }

