    const confirmDelete = () => {
      if (announcementToDelete.value) {
        const index = announcements.value.findIndex(ann => ann.id === announcementToDelete.value.id)
        if (index !== -1) {
          announcements.value.splice(index, 1)
          closeDeleteModal()
          alert('')
        }
      }
    }

