                ...parsedSavedInfo, // 
                username: parsedSavedInfo.username || parsedUser.username || '', // 
                avatar: parsedSavedInfo.avatar || userInfo.value.avatar
              }
            } catch (error) {
              console.error(':', error)
              userInfo.value = {
                ...parsedUser,
                username: parsedUser.username || '',
                avatar: userInfo.value.avatar
              }
            }
