import { createRouter, createWebHistory } from 'vue-router'
import Login from '@/views/Login.vue'
import Home from '@/views/Home.vue'
import Employee from '@/views/Employee.vue'
import Approval from '@/views/Approval.vue'
import Attendance from '@/views/Attendance.vue'
import Announcement from '@/views/Announcement.vue'
import FileManager from '@/views/FileManager.vue'
import UserManagement from '@/views/UserManagement.vue'
import System from '@/views/System.vue'
import Profile from '@/views/Profile.vue'

// 
import UserProfile from '@/views/user/UserProfile.vue'
import UserHome from '@/views/user/UserHome.vue'
import MyWorkbench from '@/views/user/MyWorkbench.vue'
import MyApproval from '@/views/user/MyApproval.vue'
import MyAttendance from '@/views/user/MyAttendance.vue'
import MyFiles from '@/views/user/MyFiles.vue'
import MyAnnouncement from '@/views/user/MyAnnouncement.vue'
import UserDataManager from '@/views/user/UserDataManager.vue'

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    meta: { requiresAuth: true, title: '' }
  },
  {
    path: '/login',
    name: 'Login',
    component: Login,
    meta: { requiresAuth: false }
  },
  
  //  - 
  {
    path: '/user',
    name: 'UserHome',
    component: UserHome,
    meta: { requiresAuth: true, title: '' }
  },
  {
    path: '/user/workbench',
    name: 'UserWorkbench',
    component: MyWorkbench,
    meta: { requiresAuth: true, title: '' }
  },
  {
    path: '/user/profile',
    name: 'UserProfile',
    component: UserProfile,
    meta: { requiresAuth: true, title: '' }
  },
  {
    path: '/user/approval',
    name: 'MyApproval',
    component: MyApproval,
    meta: { requiresAuth: true, title: '' }
  },
  {
    path: '/user/attendance',
    name: 'MyAttendance',
    component: MyAttendance,
    meta: { requiresAuth: true, title: '' }
  },
  {
    path: '/user/files',
    name: 'MyFiles',
    component: MyFiles,
    meta: { requiresAuth: true, title: '' }
  },
  {
    path: '/user/announcement',
    name: 'MyAnnouncement',
    component: MyAnnouncement,
    meta: { requiresAuth: true, title: '' }
  },
  {
    path: '/user/data-manager',
    name: 'UserDataManager',
    component: UserDataManager,
    meta: { requiresAuth: true, title: '' }
  },
  
  // 
  {
    path: '/admin/employee',
    name: 'Employee',
    component: Employee,
    meta: { requiresAuth: true, title: '' }
  },
  {
    path: '/admin/approval',
    name: 'Approval',
    component: Approval,
    meta: { requiresAuth: true, title: '' }
  },
  {
    path: '/admin/attendance',
    name: 'Attendance',
    component: Attendance,
    meta: { requiresAuth: true, title: '' }
  },
  {
    path: '/admin/announcement',
    name: 'Announcement',
    component: Announcement,
    meta: { requiresAuth: true, title: '' }
  },
  {
    path: '/admin/file',
    name: 'FileManager',
    component: FileManager,
    meta: { requiresAuth: true, title: '' }
  },
  {
    path: '/admin/user',
    name: 'UserManagement',
    component: UserManagement,
    meta: { requiresAuth: true, title: '' }
  },
  {
    path: '/admin/system',
    name: 'System',
    component: System,
    meta: { requiresAuth: true, title: '' }
  },
  {
    path: '/admin/profile',
    name: 'Profile',
    component: Profile,
    meta: { requiresAuth: true, title: '' }
  },
  
  // 
  {
    path: '/employee',
    redirect: '/admin/employee'
  },
  {
    path: '/approval',
    redirect: '/admin/approval'
  },
  {
    path: '/attendance',
    redirect: '/admin/attendance'
  },
  {
    path: '/announcement',
    redirect: '/admin/announcement'
  },
  {
    path: '/file',
    redirect: '/admin/file'
  },
  {
    path: '/user-management',
    redirect: '/admin/user'
  },
  {
    path: '/system',
    redirect: '/admin/system'
  },
  {
    path: '/profile',
    redirect: '/admin/profile'
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

// 
router.beforeEach((to, from, next) => {
  console.log(':', { from: from.path, to: to.path, requiresAuth: to.meta.requiresAuth })
  
  // 
  if (to.path === '/login') {
    next()
    return
  }
  
  // token
  if (to.meta.requiresAuth) {
    const token = localStorage.getItem('token')
    if (!token) {
      console.log('')
      next('/login')
      return
    }
  }
  
  // 
  next()
})

export default router 