<template>
  <div class="login-bg">
    <!-- / -->
    <div class="login-container">
      <div class="login-card">
        <div class="logo-section">
          <div class="logo-icon">
            <a-icon type="robot" />
          </div>
          <h1 class="system-title">Smart OA </h1>
          <div class="accent-underline"></div>
          <p class="subtitle">  </p>
          
          <!--  -->
          <div class="system-switch">
            <div class="switch-tabs">
              <div 
                class="switch-tab" 
                :class="{ active: selectedSystem === 'admin' }"
                @click="handleSystemChange('admin')"
              >
                <a-icon type="setting" />
                
              </div>
              <div 
                class="switch-tab" 
                :class="{ active: selectedSystem === 'user' }"
                @click="handleSystemChange('user')"
              >
                <a-icon type="user" />
                
              </div>
            </div>
          </div>
        </div>

        <!--  -->
        <a-form
          v-if="isLogin"
          :model="loginForm"
          @finish="onLoginFinish"
          autocomplete="off"
          layout="vertical"
          class="login-form"
        >
          <a-form-item
            name="username"
            :rules="[{ required: true, message: '!' }]"
            label=""
          >
            <div class="custom-input-wrapper">
              <a-icon type="user" class="input-icon" />
              <input 
                v-model="loginForm.username" 
                type="text"
                placeholder="" 
                class="custom-input"
                autocomplete="off"
              />
            </div>
          </a-form-item>
          <a-form-item
            name="password"
            :rules="[{ required: true, message: '!' }]"
            label=""
          >
            <div class="custom-input-wrapper">
              <a-icon type="lock" class="input-icon" />
              <input 
                v-model="loginForm.password" 
                type="password"
                placeholder="" 
                class="custom-input"
                autocomplete="off"
              />
            </div>
          </a-form-item>
          <a-form-item>
            <a-button 
              type="primary" 
              html-type="submit" 
              block 
              size="large" 
              :loading="loading"
              class="login-button"
            >
              <a-icon type="login" />
              
            </a-button>
          </a-form-item>
          <div class="status-inline" v-if="backendStatus !== null">
            <a-icon :type="backendStatus ? 'check-circle' : 'close-circle'" :class="['status-dot', backendStatus ? 'ok' : 'bad']" />
            <span :class="['status-label', backendStatus ? 'ok' : 'bad']">{{ backendStatus ? '' : '' }}</span>
            <a @click="checkBackendStatus" class="status-action"></a>
          </div>
          <!--  -->
          <div class="form-footer" v-if="selectedSystem === 'user'">
            <a @click="toggleForm" class="switch-link">? </a>
          </div>
        </a-form>
        
        <!--  -->
        <a-form
          v-else
          :model="registerForm"
          @finish="onRegisterFinish"
          autocomplete="off"
          layout="vertical"
          class="register-form"
        >
          <a-form-item
            name="username"
            :rules="[
              { required: true, message: '!' },
              { min: 3, message: '3!' },
              { pattern: /^[a-zA-Z0-9_]+$/, message: '!' }
            ]"
            label=""
          >
            <div class="custom-input-wrapper">
              <a-icon type="user" class="input-icon" />
              <input 
                v-model="registerForm.username" 
                type="text"
                placeholder="" 
                class="custom-input"
                autocomplete="off"
              />
            </div>
          </a-form-item>
          <a-form-item
            name="password"
            :rules="[
              { required: true, message: '!' },
              { min: 6, message: '6!' }
            ]"
            label=""
          >
            <div class="custom-input-wrapper">
              <a-icon type="lock" class="input-icon" />
              <input 
                v-model="registerForm.password" 
                type="password"
                placeholder="" 
                class="custom-input"
                autocomplete="off"
              />
            </div>
          </a-form-item>
          <a-form-item
            name="confirmPassword"
            :rules="[
              { required: true, message: '!' },
              { validator: validateConfirmPassword, message: '!' }
            ]"
            label=""
          >
            <div class="custom-input-wrapper">
              <a-icon type="safety" class="input-icon" />
              <input 
                v-model="registerForm.confirmPassword" 
                type="password"
                placeholder="" 
                class="custom-input"
                autocomplete="off"
              />
            </div>
          </a-form-item>

          <a-form-item>
            <a-button 
              type="primary" 
              html-type="submit" 
              block
              size="large" 
              :loading="loading"
              class="register-button"
            >
              <a-icon type="user-add" />
              
            </a-button>
          </a-form-item>
          <div class="form-footer">
            <a @click="toggleForm" class="switch-link">?</a>
          </div>
        </a-form>
      </div>

      <!--  -->
      <div class="info-section minimal">
        <div class="service-status">
          <h3 class="section-title"></h3>
          <p class="status-message" :class="{ error: !backendStatus }">
            {{ backendStatus ? '' : '' }}
          </p>
          <a-button type="default" size="small" @click="checkBackendStatus" class="status-check-btn"></a-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { defineComponent, ref, onMounted, reactive } from 'vue'
import { useRouter } from 'vue-router'
import { message } from 'ant-design-vue'
import { setCurrentUser } from '@/utils/userDataManager'
import { captureError, setUser, addBreadcrumb } from '@/utils/sentry'

export default defineComponent({
  name: 'Login',
  setup() {
    const router = useRouter()
    
    // 
    const isLogin = ref(true)
    const loading = ref(false)
    const backendStatus = ref(null)
    const selectedSystem = ref('user') // 

    //  - 
    const loginForm = reactive({
      username: '',
      password: ''
    })

    //  - 
    const registerForm = reactive({
      username: '',
      password: '',
      confirmPassword: ''
    })

    // / - 
    const toggleForm = () => {
      if (selectedSystem.value === 'admin') {
        message.warning('')
        return
      }
      isLogin.value = !isLogin.value
      // 
      loginForm.username = ''
      loginForm.password = ''
      registerForm.username = ''
      registerForm.password = ''
      registerForm.confirmPassword = ''
    }

    // 
    const handleSystemChange = (system) => {
      selectedSystem.value = system
      if (system === 'admin') {
        isLogin.value = true // 
      }
      // 
      Object.assign(loginForm, { username: '', password: '' })
      Object.assign(registerForm, { username: '', password: '', confirmPassword: '' })
    }

    // 
    const validateConfirmPassword = (rule, value) => {
      if (value && value !== registerForm.password) {
        return Promise.reject('!')
      }
      return Promise.resolve()
    }

    // 
    const getRegisteredUsers = () => {
      try {
        const raw = localStorage.getItem('users')
        return raw ? JSON.parse(raw) : []
      } catch (e) {
        return []
      }
    }
    const saveRegisteredUsers = (users) => {
      localStorage.setItem('users', JSON.stringify(users))
    }
    const addRegisteredUser = (username, password) => {
      const users = getRegisteredUsers()
      users.push({ username, password })
      saveRegisteredUsers(users)
    }

    // 
    const checkBackendStatus = async () => {
      // 
      const backendUrl = import.meta.env?.VITE_API_BASE || process.env.VUE_APP_API_BASE
      if (!backendUrl) {
        backendStatus.value = null // 
        return
      }
      
      try {
        const response = await fetch(`${backendUrl}/api/auth/me`)
        backendStatus.value = response.ok
        if (response.ok) {
          message.success('')
        } else {
          message.error('')
        }
      } catch (error) {
        backendStatus.value = false
        message.error('')
        
        // 
        captureError(error, {
          component: 'Login',
          action: 'checkBackendStatus',
          context: ''
        })
      }
    }

    // 
    const onLoginFinish = (values) => {
      loading.value = true
      console.log(':', values)
      
      // 
      if (!values.username || !values.password) {
        message.error('')
        loading.value = false
        return
      }
      
      // 
      if (selectedSystem.value === 'user' && values.username === 'admin') {
        message.error('')
        loading.value = false
        return
      }
      
      // 
      if (values.username === 'admin' && selectedSystem.value !== 'admin') {
        message.error('')
        loading.value = false
        return
      }
      
      //  - 
      setTimeout(() => {
        loading.value = false
        
        if (selectedSystem.value === 'admin') {
          //  admin/admin123
          if (values.username !== 'admin' || values.password !== 'admin123') {
            message.error('admin/admin123')
            return
          }
        } else {
          // 
          const users = getRegisteredUsers()
          const found = users.find(u => u.username === values.username)
          if (!found) {
            message.error('')
            return
          }
          if (found.password !== values.password) {
            message.error('')
            return
          }
        }
        
        // localStorage
        const userData = {
          username: values.username,
          password: values.password,
          avatar: `https://api.dicebear.com/7.x/avataaars/svg?seed=${values.username}`,
          department: values.username === 'admin' ? '' : '',
          position: values.username === 'admin' ? '' : '',
          bio: values.username === 'admin' ? '' : '',
          employeeId: values.username === 'admin' ? 'ADMIN001' : 'EMP001'
        }
        
        localStorage.setItem('user', JSON.stringify(userData))
        localStorage.setItem('username', values.username)
        // token
        localStorage.setItem('token', `token_${values.username}_${Date.now()}`)
        
        // 
        setCurrentUser(values.username)
        
        //  Sentry 
        setUser(userData)
        
        // 
        addBreadcrumb({
          category: 'auth',
          message: '',
          level: 'info',
          data: {
            username: values.username,
            system: selectedSystem.value
          }
        })
        
        // recentRegisteredUser
        localStorage.removeItem('recentRegisteredUser')
        
        // 
        if (selectedSystem.value === 'user') {
          router.push('/user')
          message.success('')
        } else {
          router.push('/')
          message.success('')
        }
      }, 400)
    }

    // 
    const onRegisterFinish = (values) => {
      // 
      if (selectedSystem.value === 'admin') {
        message.error('')
        return
      }
      
      loading.value = true
      console.log(':', values)
      
      // 
      if (!values.username || !values.password || !values.confirmPassword) {
        message.error('')
        loading.value = false
        return
      }
      
      if (values.password !== values.confirmPassword) {
        message.error('')
        loading.value = false
        return
      }
      
      // 
      setTimeout(() => {
        loading.value = false
        
        // 
        const users = getRegisteredUsers()
        if (users.some(u => u.username === values.username)) {
          message.error('')
          return
        }
        
        // 
        addRegisteredUser(values.username, values.password)
        
        // 
        localStorage.setItem('recentRegisteredUser', JSON.stringify({
          username: values.username,
          avatar: `https://api.dicebear.com/7.x/avataaars/svg?seed=${values.username}`
        }))
        
        message.success('')
        // token
        isLogin.value = true
        Object.assign(loginForm, { username: values.username, password: '' })
        Object.assign(registerForm, { username: '', password: '', confirmPassword: '' })
      }, 400)
    }

    onMounted(() => {
      checkBackendStatus()
    })

    return {
      isLogin,
      loading,
      backendStatus,
      loginForm,
      registerForm,
      toggleForm,
      validateConfirmPassword,
      checkBackendStatus,
      onLoginFinish,
      onRegisterFinish,
      selectedSystem,
      handleSystemChange
    }
  }
})
</script>

